<template>
  <base-component>
    <Breadcrumb :style="{margin: '15px 0 15px 185px', position: 'fixed'}">
      <BreadcrumbItem to="/home"></BreadcrumbItem>
      <BreadcrumbItem to="/v2/dbDrcBuildV2">DB DRC </BreadcrumbItem>
    </Breadcrumb>
    <Content class="content"
             :style="{padding: '10px', background: '#ffffff', margin: '50px 0 111px 185px', zIndex: '1'}">
      <Row :gutter=10 align="middle">
        <Col span="18">
          <Form :model="formItem" :label-width="100" style="margin-right: 20px;margin-top: 10px">
            <FormItem label="" :required=true>
              <Select
                v-model="meta.dbName"
                filterable
                :disabled="meta.fixDb"
                placeholder=""
                @on-change="selectDb"
                :remote-method="getExistDb"
                :loading="dataLoading">
                <Option v-for="(option, index) in meta.dbOptions" :value="option.dbName" :key="index">
                  {{ option.dbName }}
                </Option>
              </Select>
            </FormItem>
            <FormItem label="" :required=true>
              <RadioGroup v-model="selectedExistReplication" button-style="solid"
                          @on-change="afterSelectExistReplication">
                <Radio v-for="items in meta.existReplicationRegionOptions"
                       :key="items.srcRegionName+' -> '+items.dstRegionName"
                       :label="items.srcRegionName+' -> '+items.dstRegionName" border>
                  {{ items.srcRegionName + ' -> ' + items.dstRegionName }}
                </Radio>
              </RadioGroup>
              <Button type="primary" icon="md-add" ghost @click="goToCreateReplication"></Button>
              <Modal v-model="createModal.open" width="1200px" @on-ok="createReplication" title="">
                <Row v-if="createModal.open">
                  <Col span="11">
                    <Card :bordered="true">
                      <template #title>
                        <Icon type="ios-pin"/>
                         region
                      </template>
                      <FormItem>
                        <Select filterable clearable v-model="formItem.srcRegionName" placeholder=""
                                @on-change="afterSwitchRegion">
                          <Option v-for="region in meta.regionOptions" :value="region" :key="region" :label="region">
                            {{ region }}
                          </Option>
                        </Select>
                      </FormItem>
                    </Card>
                  </Col>
                  <Col span="2" style="text-align: center">
                    <Button size="small" shape="circle" type="default" :loading="dataLoading" @click="getRegionOptions">
                      ->
                    </Button>
                  </Col>
                  <Col span="11">
                    <Card :bordered="true">
                      <template #title>
                        <Icon type="ios-pin"/>
                         region
                      </template>
                      <FormItem>
                        <Select filterable clearable v-model="formItem.dstRegionName" placeholder=""
                                @on-change="afterSwitchRegion">
                          <Option v-for="region in meta.regionOptions" :value="region" :key="region" :label="region">
                            {{ region }}
                          </Option>
                        </Select>
                      </FormItem>
                    </Card>
                  </Col>
                </Row>
              </Modal>
            </FormItem>
            <Divider orientation="left"></Divider>
            <Card style="width:100%">
              <tables :table-data="drcConfig.logicTableSummaryDtos" :data-loading="configDataLoading"
                      :src-region="meta.srcRegionName" :dst-region="meta.dstRegionName" :db-name="meta.dbName"
                      :db-names="drcConfig.dbNames" @updated="getDrcConfig"
              />
            </Card>
            <Divider orientation="left">DB</Divider>
            <Card style="width:100%">
              <mha-replication-panel :mha-replications="drcConfig.mhaReplications"/>
            </Card>
          </Form>
        </Col>
      </Row>
    </Content>
  </base-component>
</template>
<script>

import tables from '@/components/v2/dbDrcBuild/tables.vue'
import MhaReplicationPanel from '@/components/v2/dbDrcBuild/mhaReplicationPanel.vue'

export default {
  components: { MhaReplicationPanel, tables },
  data () {
    return {
      createModal: {
        open: false
      },
      dataLoading: false,
      configDataLoading: false,
      selectedExistReplication: this.$route.query.srcRegionName + ' -> ' + this.$route.query.dstRegionName,
      formItem: {
        srcRegionName: null,
        dstRegionName: null
      },
      drcConfig: {},
      meta: {
        dbName: this.$route.query.dbName,
        srcRegionName: this.$route.query.srcRegionName,
        dstRegionName: this.$route.query.dstRegionName,
        fixDb: this.$route.query.fixDb,
        regionOptions: [],
        dbOptions: [],
        existReplicationRegionOptions: []
      },
      constants: {
        DAL_CLUSTER_MODE: 1
      }
    }
  },
  methods: {
    getParams: function () {
      const param = {}
      param.dbName = this.meta.dbName
      param.srcRegionName = this.meta.srcRegionName
      param.dstRegionName = this.meta.dstRegionName
      return param
    },
    async getExistDb (dbName) {
      if (dbName === null || dbName.length === null || dbName.length <= 0) {
        return []
      }
      const that = this
      that.dataLoading = true
      await that.axios.get('/api/drc/v2/autoconfig/getExistDb', {
        params: {
          dbName: dbName
        }
      })
        .then(response => {
          const data = response.data.data
          if (data) {
            that.meta.dbOptions = data
          } else {
            that.$Message.warning('DB')
          }
        })
        .catch(message => {
          that.$Message.error('DB: ' + message)
        })
        .finally(() => {
          that.dataLoading = false
        })
    },
    async selectDb () {
      await this.getRegionOptions()
      await this.getExistReplicationRegionOptions()
      await this.getDrcConfig()
    },
    async getDrcConfig () {
      this.resetPath()
      const params = this.getParams()
      this.drcConfig = {}
      if (!params.dbName) {
        this.$Message.warning('')
        return
      }
      if (!params.srcRegionName || !params.dstRegionName) {
        return
      }
      if (params.srcRegionName === params.dstRegionName) {
        return
      }
      this.configDataLoading = true
      await this.axios.get('/api/drc/v2/autoconfig/getDbDrcConfig', {
        params: {
          dbName: params.dbName,
          srcRegionName: params.srcRegionName,
          dstRegionName: params.dstRegionName
        }
      })
        .then(response => {
          const data = response.data.data
          const success = data && response.data.status === 0
          if (success) {
            this.drcConfig = data
            this.$Message.success('DRC ')
          } else {
            this.$Message.warning('' + response.data.message)
          }
        })
        .catch(message => {
          this.$Message.error(': ' + message)
        })
        .finally(() => {
          this.configDataLoading = false
        })
    },
    async afterSwitchRegion () {
    },
    async afterSelectExistReplication () {
      console.log('afterSelectExistReplication ', this.selectedExistReplication)
      const ret = this.selectedExistReplication.split('->')
      this.meta.srcRegionName = ret[0].trim()
      this.meta.dstRegionName = ret[1].trim()
      this.getDrcConfig()
    },
    async getRegionOptions () {
      const that = this
      that.dataLoading = true
      that.meta.regionOptions = []
      const params = this.getParams()
      await that.axios.get('/api/drc/v2/autoconfig/regionOptions', {
        params: {
          mode: this.constants.DAL_CLUSTER_MODE,
          dbName: params.dbName
        }
      })
        .then(response => {
          const data = response.data.data
          const success = data && response.data.status === 0
          if (success) {
            that.meta.regionOptions = data
            if (data.indexOf(that.formItem.srcRegionName) === -1) {
              that.formItem.srcRegionName = null
            }
            if (data.indexOf(that.formItem.dstRegionName) === -1) {
              that.formItem.dstRegionName = null
            }
            that.$Message.success(' ' + that.meta.regionOptions.length + ' ')
          } else {
            that.formItem.srcRegionName = null
            that.formItem.dstRegionName = null
            that.$Message.warning('')
          }
        })
        .catch(message => {
          that.$Message.error(': ' + message)
        })
        .finally(() => {
          that.dataLoading = false
        })
    },
    async getExistReplicationRegionOptions () {
      const that = this
      that.dataLoading = true
      that.meta.existReplicationRegionOptions = []
      const params = this.getParams()
      await that.axios.get('/api/drc/v2/autoconfig/getExistDbReplicationDirections', {
        params: {
          dbName: params.dbName
        }
      })
        .then(response => {
          const data = response.data.data
          const success = data && response.data.status === 0
          if (success) {
            that.meta.existReplicationRegionOptions = data
            that.$Message.success(' ' + that.meta.existReplicationRegionOptions.length + ' ')
            console.log('total exist direction: ' + that.meta.existReplicationRegionOptions.length)
          } else {
            that.meta.srcRegionName = null
            that.meta.dstRegionName = null
            that.$Message.warning('')
          }
        })
        .catch(message => {
          that.$Message.error(': ' + message)
        })
        .finally(() => {
          that.dataLoading = false
        })
    },
    async goToCreateReplication () {
      this.createModal.open = true
    },
    async createReplication () {
      const that = this
      const params = {
        srcRegionName: this.formItem.srcRegionName,
        dstRegionName: this.formItem.dstRegionName,
        dbName: this.meta.dbName
      }
      await that.axios.post('/api/drc/v2/autoconfig/mhaDbReplication/create', params)
        .then(response => {
          const data = response.data
          const success = data.status === 0
          if (success) {
            that.$Message.success('')
            that.$emit('finished')
          } else {
            that.$Message.warning(': ' + data.message)
          }
        })
        .catch(message => {
          that.$Message.error(': ' + message)
        })
        .finally(() => {
          that.dataLoading = false
        })
    },
    resetPath () {
      this.$router.replace({
        query: {
          dbName: this.meta.dbName,
          srcRegionName: this.meta.srcRegionName,
          dstRegionName: this.meta.dstRegionName,
          fixDb: this.meta.fixDb
        }
        // eslint-disable-next-line handle-callback-err
      }).catch(() => {
      })
    },
    flattenObj (ob) {
      const result = {}
      for (const i in ob) {
        if ((typeof ob[i]) === 'object' && !Array.isArray(ob[i])) {
          const temp = this.flattenObj(ob[i])
          for (const j in temp) {
            result[i + '.' + j] = temp[j]
          }
        } else {
          result[i] = ob[i]
        }
      }
      return result
    }
  },
  computed: {},
  created () {
    // this.getRegions()
    this.axios.get('/api/drc/v2/permission/meta/mhaReplication/modify').then((response) => {
      if (response.data.status === 403) {
        this.$router.push('/nopermission')
        return
      }
      this.getRegionOptions()
      this.getExistReplicationRegionOptions()
      this.getDrcConfig()
    })
  }
}
</script>

<style>
.drawer-footer {
  width: 100%;
  bottom: 0;
  left: 0;
  border-top: 1px solid #e8e8e8;
  padding: 10px 16px;
  text-align: right;
  background: #fff;
}
</style>
